<template>
  <div class="about">
    <h3>{{title}}</h3>
    <img :src="imgUrl" alt="">
    <!-- 注意：这里图片需要这样渲染数据，:src="" -->
    <p>{{summary}}</p>
  </div>
</template>
<script>
import axios from "axios-jsonp-pro";
export default {
  name:'About',
  data(){
    return {
      imgUrl:"",
      title:"",
      summary:""
    }
  },
  mounted(){
    var id = this.$route.params.id;
    // console.log(id);
    var url = "https://douban.uieee.com/v2/movie/subject/";
    axios.jsonp(url+id).then(res=>{
      this.imgUrl = res.images.small;
      this.title = res.title;
      this.summary = res.summary;
    })
  }
}
</script>
<style lang="scss" scoped>
.about{
  text-align: center;
}
.about p{
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  text-indent: 34px;
}
</style>

